<template>
    <div class="row">
        <div class="reg-box">
            <el-row>
                <el-button type="primary" v-if="complete === false" @click="create"></el-button>
                <div class="add-box" v-show="complete">
                    <div>
                        <h2></h2>
                        <p>
                            
                        </p>
                    </div>
                    <div class="info-box">
                        <span>Ripple </span>
                        <div>{{info.address}}</div>
                    </div>
                    <div class="info-box">
                        <span></span>
                        <div>{{info.secret}}</div>
                    </div>
                </div>
                <div style="margin-top: 0.5em" v-if="complete === true">
                    <el-button type="primary" @click="login"></el-button>
                </div>
            </el-row>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CreateAddress',
    data () {
        return {
            info: {},
            complete: false
        };
    },
    methods: {
        create: function () {
            const self = this;
            const rippletest = 'https://faucet.altnet.rippletest.net/accounts';
            this.$http.post(rippletest).then(response => {
                self.saveToDb(response.body);
            }, response => {
                // error callback
            });
        },
        saveToDb: function (account) {
            this.$http.post('/api/reg', account.account).then(response => {
                let userInfo = response.body;
                console.log(response.body.reslut);
                if (userInfo.reslut === true) {
                    this.info = userInfo.data;
                    this.complete = true;
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                } else {
                    this.info = {};
                    this.$message.error('');
                    // this.$store.commit('notLogin');
                }
            }, response => {
                // error callback
            });
        },
        login: function () {
            this.$router.push({path: '/general'});
        }
    }
};
</script>

<style scoped>
.reg-box{
    padding: 1em 2em 0 2em;
}
.info-box{
    margin-top: 1em;
    color: #1F1F1E;
}
</style>



